/*
$spacers: (
  0: 0,
  1: $spacer * 0.25,
  2: $spacer * 0.5,
  3: 27px,
  4: $spacer * 1.5,
  5: $spacer * 3,
  6: $spacer * 4,
  7: $spacer * 5,
  8: $spacer * 5.4,
  9: $spacer * 7,
  10: $spacer * 8,
);
*/
.block--o .icon-row {
  margin-bottom: -1.3rem;
}
@media screen and (min-width: 768px) {
  .block--o .icon-row .media-right {
    flex-direction: row-reverse;
  }
}
.block--o .img-icon {
  width: 100%;
  margin-bottom: 0;
  margin-top: 0;
}
.block--o .img-icon img {
  width: 100%;
  height: 100px;
  object-fit: contain;
  object-position: left;
}
@media screen and (min-width: 1025px) {
  .block--o.icons-small-desktop .img-icon img {
    height: 70px;
  }
}